import React, {useEffect, useState} from 'react';
import NewsCard from './NewsCard';
import './News.css';
import defaultImage from'./defaultImage.png';

const News = ({dragWidget}) => {

	// const [author, setAuthor] = useState('');
	// const [content, setContent] = useState('');
	// const [description, setDescription] = useState('');
	// const [title, setTitle] = useState('');
	const [articles, setArticles] = useState([]);


	async function generateNews() {
		let response = await fetch('http://newsapi.org/v2/top-headlines?sources=google-news-au&apiKey=b235be02ca3e401b9cf403aef051f564');
		let data = await response.json();
		setArticles(data.articles)
	}

	useEffect(() => {
		generateNews();

	},[])

	return(
		<div id='news' className='news-container widget' onDoubleClick={dragWidget}>
			{
				articles.map((article, i) => {
					return (
						<NewsCard 
							key={i}
							title={article.title}
							description={article.description}
							content={article.content}
							author={article.author}
							image={ article.urlToImage === "" ? defaultImage : article.urlToImage } 
							url={article.url}
							publishedAt={article.publishedAt}
						/>
					);
				})
			}
		</div>
		);
}

export default News;