import React from 'react';
import './Header.css';
import Time from '../dateAndTime/Time'

export class Header extends React.Component {
	constructor(props) {
		super(props);
		this.state = { time: 'Good Morning'}
	}

	findHour() {
		const today = new Date();
		return today.getHours()
	}

	whichGreeting() {
		let currentHour = this.findHour();
			if (0 <= currentHour &&  currentHour < 12 ) {
				return 'Good Morning';
			} else if (12 <= currentHour && currentHour < 17 ) {
				return 'Good Afternoon';
			} else {
				return 'Good Evening';
			}
		}

	toggleOnClickPomo() {
		if (document.querySelector(".pomoBox").classList.contains('show')) {
			document.querySelector(".pomoBox").classList.remove('show');
			setTimeout(()=>{
				document.querySelector('.pomoBox').style.display = 'none';
			}, 500)
		} else {
			document.querySelector('.pomoBox').style.display = 'block';
			setTimeout(()=>{
				document.querySelector(".pomoBox").classList.add('show');
			}, 10)
		}
	}

	render() {
		return (
			<div className='header-container'>
				<div>
					<h1 className='title'>{ this.whichGreeting() }, Damien</h1>
					<Time />
				</div>
			</div>
		);
	}
}
