import React from 'react';
import './Pomodoro.css';
import PomoSettings from './pomoSettings/PomoSettings.js';
import moreIcon from './more.png';

class Pomodoro extends React.Component {
	constructor(props){
		super(props);
		this.state = {
			timerSecond: 0,
			cycle: 'work',
			workTime: 25,
			breakTime: 5,
			currentTime: '25:00',
			timerRunning: false,
			settings: false,
			fullScreen: false
		};
		this.toggleSettings = this.toggleSettings.bind(this)
		this.incrementBreakTime = this.incrementBreakTime.bind(this);
		this.decrementBreakTime = this.decrementBreakTime.bind(this);
		this.incrementWorkTime = this.incrementWorkTime.bind(this);
		this.decrementWorkTime = this.decrementWorkTime.bind(this);
		this.handleStartButton = this.handleStartButton.bind(this);
		
		// this.startTimer = this.startTimer.bind(this);
	}
	
	toggleSettings() {
		if (this.state.settings === false) {
			this.setState({settings: true})
			document.querySelector('.pomo-settings-box').style.display ='block';
		} else {
			this.setState({settings: false})
			document.querySelector('.pomo-settings-box').style.display='none';
			//Adjust any updates on time displayed
			if (this.state.cycle === 'work') {
				this.setState({currentTime: this.state.workTime + ':00'});
			} else {
				this.setState({currentTime: this.state.breakTime +':00'});
			}
		}
	}

	incrementBreakTime() {
		if(this.state.timerRunning === false) {
			this.setState({breakTime: this.state.breakTime + 1})
		}
	}

	decrementBreakTime() {
		if(this.state.timerRunning === false) {
			this.setState({breakTime: this.state.breakTime - 1})	
		}
	}

	incrementWorkTime() {
		if(this.state.timerRunning === false) {
			this.setState({workTime: this.state.workTime + 1})	
		}
	}

	decrementWorkTime() {
		if(this.state.timerRunning === false) {
			this.setState({workTime: this.state.workTime - 1})	
		}
	}

	handleStartButton() {
		if (this.state.timerRunning === false) {
			document.querySelector('.pomo-button').innerHTML = 'Exit';
			this.setState({timerRunning: true});
			this.startTimer();		 
		} else {
			document.querySelector('.pomo-button').innerHTML = 'Start'
			this.setState({
				timerRunning: false,
				currentTime: this.state.workTime + ':00',
				cycle: 'work'
			});
			clearInterval(this.state.timerSeconds);
		}
	}

	startTimer(){
		let timerDuration;
		if (this.state.cycle === 'work') {
			timerDuration = this.state.workTime;
			let time = timerDuration * 60
			let runningTimer = setInterval(() => {
				this.setState({timerSeconds: runningTimer})
				time = time - 1;
				let minutes = Math.floor(time / 60)
				let seconds = time - (minutes * 60)
				minutes = minutes < 10 ? '0' + minutes : minutes
				seconds = seconds < 10 ? '0' + seconds : seconds
				this.setState({currentTime: minutes + ':' + seconds})
				if (this.state.timerRunning === false) {
					this.setState({
						currentTime: this.state.workTime + ':00',
						cycle: 'work'
					});
					clearInterval(this.state.timerSeconds)
				}
				if (minutes === 0 && seconds === 0) {
					this.setState({
						cycle: 'break',
						timerRunning: false,
						currentTime: this.state.breakTime
					});
					clearInterval(this.state.timerSeconds)
					document.querySelector('.pomo-button').innerHTML = 'Break'
				}
			}, 1000)

		} else {
			timerDuration = this.state.breakTime;
			let time = timerDuration * 60;
			let runningTimer = setInterval(() => {
				this.setState({timerSeconds: runningTimer})
				time = time - 1;
				let minutes = Math.floor(time / 60)
				let seconds = time - (minutes * 60)
				this.setState({currentTime: minutes + ':' + seconds})

				if (minutes === 0 && seconds === 0) {
					this.setState({
						cycle: 'work',
						timerRunning: false,
						currentTime: this.state.workTime
					});
					clearInterval(this.state.timerSeconds)
					document.querySelector('.pomo-button').innerHTML = 'Start'
				}
			}, 1000)
		}
	}


	render() {
		return(
			<div id='pomo' className='pomo-container widget' onDoubleClick={this.props.dragWidget}>
				<div className='pomo-top-section'>
					<img className='more-icon grow' src={moreIcon} alt='icon' onClick={this.toggleSettings}/>
				</div>
				<div className='pomo-middle-section'>
					<div className='countdown-ring'>
						<h1 className='countdown'>{this.state.currentTime}</h1>
					</div>
				</div>
				<div className='pomo-bottom-section'>
					<div className='pomo-button-box'> 
						<button className='pomo-button' type='button' value='start' onClick={this.handleStartButton}>Start</button>
					</div>
				</div>
				<PomoSettings 
					breakTime={this.state.breakTime} 
					workTime={this.state.workTime} 
					toggleSettings={this.toggleSettings}
					incrementWorkTime={this.incrementWorkTime}
					decrementWorkTime={this.decrementWorkTime}
					incrementBreakTime={this.incrementBreakTime}
					decrementBreakTime={this.decrementBreakTime}
					/>
			</div>
		)
	}
}

export default Pomodoro;